.themetoggle {
  width: 40px;
  height: 24px;
  background-image: linear-gradient(#4f2ee8 0%, #dcdcdc 100%);
  border-radius: 37%;
  transition: background-image .35s cubic-bezier(.4, 0, .2, 1) 5ms;
  position: relative;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, .1);
}

.themetoggleswitch {
  width: 18px;
  height: 18px;
  background-image: linear-gradient(#fff, #f7f7f7);
  border-radius: 50%;
  transition: transform .35s cubic-bezier(.4, 0, .2, 1) 5ms;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-100%, -50%);
}

