.book-overlay {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 20;
  background-color: rgba(17, 17, 17, .4);
  transition: opacity .25s linear, visibility .25s linear;
  position: fixed;
  top: 0;
  left: 0;
}

.modal-book {
  opacity: 0;
  visibility: hidden;
  width: 100%;
  max-width: 335px;
  z-index: 30;
  background-color: #fff;
  border: 2px solid #111;
  border-radius: 18px;
  padding: 40px 24px;
  transition: all .25s linear;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

body.dark .modal-book {
  background-color: #202024;
}

.modal-book.active, .book-overlay.active {
  opacity: 1;
  visibility: visible;
}

