.shopping-section {
    background-color: var(--main-background-color);
}

.shopping-list-title {
    color: var(--primary-color-text);
    font-size: 32px;
    font-weight: 700;
    line-height: 118.75%;
    letter-spacing: -1.28px;

    margin-bottom: 120px;
}

.title-list {
    color: var(--primary-stilization-color);
}

.shopping-list-is-empty-message {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 14px;
}

.shopping-list-is-empty-text {
    width: 244px;

    color: rgba(0, 0, 0, 0.60);
    text-align: center;
    font-size: 14px;
    line-height: 128.571%; 
    letter-spacing: -0.28px;
}

.shopping-list-is-empty-img {
    background:lightgray 50%, url(../images/shopping-list/Books.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 265px;
    height: 198px;
}

.shopping-list {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.shopping-list-item {
    display: flex;
    gap: 14px;
    width: 335px;
    height: 170px;
    padding: 14px;

    border-radius: 16px;
    border: 2px solid rgba(79, 46, 232, 0.40);
    background: var(--primary-color-text-dark-theme);
}

.shopping-list-item-content-wrapper {
    width: 100%;
}

.shopping-list-item-img {
    border-radius: 8px;
}

.shopping-list-item-head {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 8px;
}

.shopping-list-item-title {
    color: var(--primary-color-text);
    font-size: 16px;
    font-weight: 700;
    line-height: 112.5%;
    letter-spacing: -0.64px;
    text-transform: uppercase;
    margin-bottom: 4px;
}

.shopping-list-item-category {
    color: #B4AFAF;
    font-size: 12px;
    line-height: 116.667%;
    letter-spacing: 0.36px;
}

@media screen and (max-width: 767px) {
    .shopping-list-item-title {
        width: 144px;
        height: 18px;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .shopping-list-item-category {
        width: 165px;
        height: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .shopping-list-item-author {
        width: 97px;
        height: 14px;
        overflow: hidden;
        text-overflow: ellipsis;
    }

    .shopping-list-item-description {
        width: 193px;
    }
    
}

.delete-btn-icon {
    width: 16px;
    height: 16px;
}

.shopping-list-delete-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 28px;
    height: 28px;
    background-color: #4F2EE8;
    border-radius: 50%;
    cursor: pointer;
}

.shopping-list-item-description {
    height: 68px;
    overflow: auto;
    color: var(--primary-color-text);
    font-size: 14px;
    line-height: 128.571%;
    letter-spacing: -0.56px;

    margin-bottom: 14px;
}

.shopping-list-item-bottom-wrapper {
    display: flex;
    justify-content: space-between;
}

.shopping-list-item-author {
    color: #B4AFAF;
    font-size: 12px;
    font-style: italic;
    line-height: 116.667%;
    letter-spacing: -0.48px;
}

.shopping-list-buy-links-list {
    display: flex;
    gap: 8px;
    align-items: center;
}

.shopping-list-buy-link {
    display: block;
}

@media screen and (max-width: 374px) {
    .shopping-list-item {
        width: 100%;
    }

    .shopping-list-item-title {
        width: 110px;
    }

    .shopping-list-item-category {
        width: 110px;
    }

    .shopping-list-item-description {
        width: 140px;
    }

    .shopping-list-item-author {
        width: 70px;
    }
}

@media screen and (min-width: 768px) {
    .shopping-list-title {
        font-size: 48px;
        line-height: 108.333%;
        letter-spacing: -1.92px;
        margin-bottom: 140px;
    }

    .shopping-list-is-empty-message {
        gap: 20px;
    }

    .shopping-list-is-empty-text {
        width: 349px;
        font-size: 18px;
        line-height: 133.333%;
        letter-spacing: -0.36px;
    }

    .shopping-list-is-empty-img {
        width: 322px;
        height: 241px;
    }

    .shopping-list-item {
        gap: 24px;
        width: 100%;
        height: 213px;
        padding: 24px;
    }

    .shopping-list-item-img {
        width: 116px;
        height: 165px;
    }

    .delete-btn-icon {
        width: 18px;
        height: 18px;
    }

    .shopping-list-delete-btn {
        width: 34px;
        height: 34px;
    }

    .shopping-list-buy-links-list {
        gap: 16px;
    }

    .shopping-list-buy-link-img {
        width: 32px;
        height: 30px;
    }

    .shopping-list-item-head {
        margin-bottom: 14px;
    }

    .shopping-list-item-description {
        margin-bottom: 32px;
        width: 508px;
        height: 53px;
    }
}

@media screen and (min-width: 1440px) {
    .shopping-list-item-description {
        width: 808px;
    }
}